.about-me{
    height: 9.5em;
    width: 9.5em;
    border-radius: 4.75em;
    text-align: center;
    color: white;
    border: none;
    padding: 20px;
    text-decoration: none;
    font-size: 16px;
    margin: 4px 2px;
}

.about-me-animatable:hover{
    -webkit-animation-name: overlays;
            animation-name: overlays;
    -webkit-animation-duration: 500ms;
            animation-duration: 500ms;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

@-webkit-keyframes overlays {
    from {opacity: 1}
    to {opacity: 0.6}
}

@keyframes overlays {
    from {opacity: 1}
    to {opacity: 0.6}
}

@media only screen and (max-width: 768px) {
    .home-content{
        -ms-flex-order: -1;
            order: -1;
    }
}


